import axios from 'axios';
import React, {useEffect, useState} from 'react';

import { useParams } from 'react-router-dom'


export default function MovieDetalis() {

    let params = useParams()
    const [movieDetalis, setMovieDetalis] = useState(null)

    async function getMovieDetalis(id)
    {
    let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=517bf03e41f4d68784b4d867d7274ef7&language=en-US`)
    setMovieDetalis(data);
    }

    useEffect(()=> {
        getMovieDetalis(params.id);
    },[])

  return (
    <>
       {movieDetalis? <div className="row py-5">
            <div className="col-md-3 position-relative">
                <img src={`https://image.tmdb.org/t/p/w500`+movieDetalis.poster_path} className="w-100" alt="" />
                <span className='position-absolute top-0 end-0 mx-3 mt-1 bg-black text-white p-2 fw-bold'>
                    {movieDetalis.vote_average}
                </span>
            </div>
            <div className="col-md-9 py-4">
                <div>
                    <h3> {movieDetalis.title} </h3>
                    <p className='text-muted'> {movieDetalis.overview} </p>
                    <ul>
                        <li className='fs-5 py-2 text-info'>runtime: {movieDetalis.runtime}</li>
                        <li className='fs-5 py-2 text-info'>vote_count: {movieDetalis.vote_count}</li>
                        <li className='fs-5 py-2 text-info'>release_date: {movieDetalis.release_date}</li>
                        <li className='fs-5 py-2 text-info'>popularity: {movieDetalis.popularity}</li>
                        <li className='fs-5 py-2 text-info'>original_language: {movieDetalis.original_language}</li>
                    </ul>
                </div>
            </div>
        </div> : <div className='vh-100 d-flex justify-content-center align-items-center'>
            <i className='fas fa-spinner fa-spin fs-1'></i>
        </div>} 
    </>
  )
}
