import axios from 'axios';
import React, {useEffect, useState} from 'react';
import { useParams } from 'react-router-dom'

export default function TvDetalis() {

    let params = useParams()
    const [tvDetalis, setTvDetalis] = useState(null)

    async function getTvTrending(id)
    {
    let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=517bf03e41f4d68784b4d867d7274ef7&language=en-US`)
    setTvDetalis(data);
    }

    useEffect(() => {
        getTvTrending(params.id);
    },[]);

  return (
    <>
     {tvDetalis? <div className="row py-5">
            <div className="col-md-3 position-relative">
                <img src={`https://image.tmdb.org/t/p/w500`+tvDetalis.poster_path} className="w-100" alt="" />
                <span className='position-absolute top-0 end-0 mx-3 mt-1 bg-black text-white p-2 fw-bold'>
                    {tvDetalis.vote_average}
                </span>
            </div>
            <div className="col-md-9 py-4">
                <div>
                    <h3> {tvDetalis.name} </h3>
                    <p className='text-muted'> {tvDetalis.overview} </p>
                    <ul>
                        <li className='fs-5 py-2 text-info'>vote_count: {tvDetalis.vote_count}</li>
                        <li className='fs-5 py-2 text-info'>first_air_date: {tvDetalis.first_air_date}</li>
                        <li className='fs-5 py-2 text-info'>popularity: {tvDetalis.popularity}</li>
                    </ul>
                </div>
            </div>
        </div> : <div className='vh-100 d-flex justify-content-center align-items-center'>
            <i className='fas fa-spinner fa-spin fs-1'></i>
        </div>} 
    </>
  )
}
